.overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  overflow-y: scroll;

  background-color: transparent;
  background-color: var(--color-shadow);

  opacity: 0;
  pointer-events: none;

  &--active {
    opacity: .5;
    pointer-events: auto;
  }
}
